.font-sans-plus-normal{
    font-family: "Plus Jakarta Sans", sans-serif;
    font-weight: 300;
    font-style: normal;
}

.font-sans-plus-normal-italic{
    font-family: "Plus Jakarta Sans", sans-serif;
