    font-style: normal;
}

html, body {
    font-family: 'Sinter', sans-serif;
    font-weight: 300;
    background-color: #000000;
    scroll-behavior: smooth;
}

.marquee {
  position: relative;
  overflow: hidden;
  background: #007bff;      /* ajuste a cor de fundo */
  color: #fff;
  white-space: nowrap;
  height: 2.5em;            /* ajuste conforme altura desejada */
  display: flex;
  align-items: center;
}

.marquee__track {
  display: flex;
  /* Define a animação de scroll: 0 → 100% da largura do conteúdo */
  animation: marquee 10s linear infinite;
}

@keyframes marquee {
  from {
    transform: translateX(0%);
  }
  to {
    transform: translateX(-20%);
  }
}

.circle-light{
  box-shadow: 0px 1px 400px -29px #0193fc9f inset;
  -webkit-box-shadow: 0px 1px 400px -29px #0193fc9f inset;
  -moz-box-shadow: 0px 1px 400px -29px #0193fc9f inset;
}

section {
  scroll-margin-top: 100px; /* ajuste esse valor conforme a altura do seu menu */
}